import React from "react"
import styled from "styled-components"
import { graphql, Link } from "gatsby"
import { Breadcrumb, Card, Pagination as BSPagination } from "react-bootstrap"
import Pagination from "rc-pagination"
import "rc-pagination/assets/index.css"

import CategoryItem from "./CategoryItem"
import MostViewest from "./MostViewest"

import SEO from "../components/seo"
import Layout from "../components/layout"

const CardFooter = styled(Card.Footer)`
  background: #fff;
  .pagination {
    margin-bottom: 0;
  }
`

export default ({ data, pageContext }) => {
  const renderItem = post => (
    <CategoryItem key={post.node.wordpress_id} post={post.node} />
  )

  return (
    <Layout>
      <SEO
        title={data.wordpressCategory.name}
        description={data.wordpressCategory.name}
      />
      <div className="container non-padding ">
        <div className="row" style={{ paddingTop: "10px", marginBottom: 10 }}>
          <div className="col-md-8">
            <Card style={{ width: "100%" }}>
              <Card.Header style={{ padding: 0 }}>
                <Breadcrumb
                  listProps={{
                    style: {
                      marginBottom: 0,
                      backgroundColor: "initial",
                      display: "flex",
                      alignItems: "center",
                    },
                  }}
                >
                  <Breadcrumb.Item
                    as={() => (
                      <Link
                        to="/"
                        style={{
                          fontWeight: "bold",
                          fontSize: 14,
                          color: "#333",
                        }}
                      >
                        Home
                      </Link>
                    )}
                  />
                  <span style={{ paddingLeft: 10, paddingRight: 10 }}>»</span>
                  <Breadcrumb.Item
                    as={() => (
                      <Link
                        to={data.wordpressCategory.path}
                        style={{
                          fontWeight: "bold",
                          fontSize: 14,
                          color: "#333",
                        }}
                      >
                        {data.wordpressCategory.name}
                      </Link>
                    )}
                  />
                </Breadcrumb>
              </Card.Header>
              <Card.Body>{pageContext.posts.map(renderItem)}</Card.Body>
              <CardFooter>
                <Pagination
                  showTitle={false}
                  defaultPageSize={1}
                  pageSize={1}
                  total={pageContext.numberOfPages}
                  current={pageContext.currentPage}
                  itemRender={(current, type, element) => {
                    if (type === "page") {
                      return (
                        <Link
                          to={
                            current == 1
                              ? data.wordpressCategory.path
                              : `${data.wordpressCategory.path}/trang/${current}`
                          }
                        >
                          {current}
                        </Link>
                      )
                    }
                    return element
                  }}
                />
              </CardFooter>
            </Card>
          </div>
          <div className="col-md-4">
            <MostViewest />
          </div>
        </div>
      </div>
    </Layout>
  )
}
export const query = graphql`
  query($id: Int!) {
    wordpressCategory(wordpress_id: { eq: $id }) {
      name
      path
    }
  }
`
